<template>
  <label class="radio">
    <input type="radio" name="gender" :value="value" @click="updateValue"/>
    <img width="50" :style="{'opacity': imageOpacity}" :src="require(`./../assets/${image}.png`)" :alt="image" />
  </label>
</template>

<script>
export default {
  methods: {
    updateValue(event){
      console.log("updateValue", event);
      this.$emit('cg', event.target.value);
    }
  },
  props:{
    value: String,
    image: String,
    imageOpacity: String
  }
};
</script>

<style scoped>
/* HIDE RADIO */
input[type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* IMAGE STYLES */
[type="radio"] + img {
  cursor: pointer;
  filter: grayscale(1);
}

/* CHECKED STYLES */
[type="radio"]:checked + img {
  filter: grayscale(0);
}
</style>