import * as mutation_types from "./mutation_types";

export default {
  [mutation_types.SAVE_AUTHOR_INFO](state, payload) {
    console.log("SAVE_AUTHOR_INFO",payload);
    state.authorInfo = { ...state.authorInfo, ...payload };
  },
  [mutation_types.SAVE_AUTHOR_ANSWERS](state, payload) {
    console.log("SAVE_AUTHOR_ANSWERS",payload);
    state.authorAnswers = {...payload };
  },
  [mutation_types.SAVE_FRIEND_INFO] (state, payload) {
    console.log("SAVE_FRIEND_INFO",payload);
    state.friendInfo = { ...state.friendInfo, ...payload };
  },
  [mutation_types.SAVE_FRIEND_ANSWERS](state, payload) {
    console.log("SAVE_FRIEND_ANSWERS",payload);
    state.friendAnswers = {...payload };
  },
  [mutation_types.IS_FRIEND] (state, payload) {
    state.isFriend = payload;
  },
  [mutation_types.RESET_STORE](state){
    state.authorInfo= {};
    state.friendInfo={};
    state.authorAnswers= {};
    state.friendAnswers= {};
    state.isFriend= false;
    state.loader= false;
    state.questions= [];
  }
};

