<template>
  <button @click="handleClick" class="button" :class="`${size} ${color}`" :disabled="disabled">
    <slot></slot>
  </button>
</template>

<script>
export default {
    methods: {
        handleClick(event){
            this.$emit('click', event.target.value);
        }
      
    },
  props: {
    size: String,
    color: String,
    disabled: Boolean
  }
};
</script>

