<template>
  <input
    class="input is-primary"
    :value="value"
    @input="updateValue"
    type="text"
    placeholder="Enter your Name"
  />
</template>

<script>
export default {
  methods: {
    updateValue(event){
      this.$emit('input', event.target.value);
    }
  },
  props: {
    value: String
  }
};
</script>

