<template>
  <div class="login-view">
    <h2 class="game-title">Friendship Challenge</h2>
    <div class="box v-center">
      <div class="field">
        <div class="control">
          <Input v-model="userName" />
          <div class="gender">
            <Radio
              value="f"
              imageOpacity="0.8"
              image="girl"
              @cg="updateGender"
              onRadioChange="this.handleRadioSelectChange"
            />
            <Radio
              value="m"
              imageOpacity="1"
              image="boy"
              @cg="updateGender"
              onRadioChange="this.handleRadioSelectChange"
            />
          </div>
        </div>
        <div class="btn-add">
          <Button
            :disabled="isLoginAllowed"
            size="is-big"
            color="is-primary"
            @click="handleLoginClick"
          >Lets Start</Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Input from "@/elements/Input.vue";
import Radio from "@/elements/Radio.vue";
import Button from "@/elements/Button.vue";
import fbConfig from "@/config/firebase_config";
export default {
  data() {
    return {
      userName: "",
      gender: ""
    };
  },

  mounted() {
    let _this = this;
    console.log("params", _this.$route.params);
    const { id: authorID } = _this.$route.params;
    if (authorID) {
      console.log({ authorID });
      _this.changeIsFriend(true);
      fbConfig
        .database()
        .ref(`/${authorID}/authorData`)
        .once("value", function(res) {
          let authorInfo = res.val();
          _this.saveAuthorInfo({
            name: authorInfo.name,
            gender: authorInfo.gender,
            key: authorID
          });
          _this.saveAuthorAnswers(authorInfo.authorAnswers);
        });
    }else{
      localStorage.removeItem("vuex");
      _this.resetTheStore();
    }
  },
  computed: {
    ...mapState(["isFriend"]),
    isLoginAllowed() {
      return this.userName.length && this.gender.length ? false : true;
    }
  },
  methods: {
    ...mapActions(["saveAuthorInfo", "saveAuthorAnswers","saveFriendInfo", "changeIsFriend", "resetTheStore"]),
    updateGender(data) {
      this.gender = data;
    },
    handleLoginClick() {
      let _this = this;
      console.log('is friend', _this.isFriend);
      if (_this.isFriend) {
        _this.saveFriendInfo({
          name: _this.userName,
          gender: _this.gender
        });
      } else {
        _this.saveAuthorInfo({
          name: _this.userName,
          gender: _this.gender
        });
      }
      _this.$router.push({ path: "/question" });
    }
  },
  components: {
    Input,
    Radio,
    Button
  }
};
</script>

